<template>
	<div class="ReplenishTaskList">
		<header><i class="iconfont icon-xiangzuo1" @click="toArea"></i><span>任务列表</span></header>
		<div class="container">
			<table class="tableContainer" cellspacing="0">
				<thead class="tableHead">
					<tr>
						<td @click="SortArr('Replenish_TaskData','ToLoc','SortCondition1',$event)">拣货库位</td>
						<td @click="SortArr('Replenish_TaskData','Sku','SortCondition2',$event)">商品代码</td>
						<td @click="SortArr('Replenish_TaskData','FromLoc','SortCondition3',$event)">存储库位</td>
						<td @click="SortArr('Replenish_TaskData','Qty','SortCondition4',$event)">数量</td>
						<td @click="SortArr('Replenish_TaskData','Status','SortCondition5',$event)">状态</td>
					</tr>
				</thead> 
				<tbody>
					<tr v-for='(dataItem,index) in Replenish_TaskData'   @click="selectTask(index)"   :class="{ 'selectedTask':index==detail_item,'normalTask':index!=detail_item}"  :id="index"  :key='index' >
						<td>{{dataItem.ToLoc}}</td>
						<td>{{dataItem.Sku}}</td>
						<td>{{dataItem.FromLoc}}</td>
						<td>{{dataItem.Qty}}</td>
						<td>{{dataItem.Status}}</td>
					</tr>
				</tbody>
			</table>
		</div>
		<div class="bottomNum">
			<div class="taskNum">
				<div>
					<span>任务数</span>
					<input  readonly  v-model="tasksNum"></input>
				</div>
				<div>
					<span>库位数</span>
					<input  readonly  v-model="LibraryNum"></input>
				</div>
			</div>
			<div class="g_doubleBtn">
				<div  @click="AbolishAllTasks">取消所有任务</div>
				<div  @click="Replenishment">开始补货</div>
			</div>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  class="g_tips">
			<div>
				<span>提示信息</span>
				<span>{{msg}}</span>
				<p  @click='tipsErrorHide(inputFocus,ifRouter)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"ReplenishTaskList",
		data(){
			return{
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				tasksNum:"",//任务数
				LibraryNum:"",//库位数
				Replenish_TaskData:[],//任务列表数据
				detail_item:0,//默认为第一个
				ifRouter:false,//是否补货完成
				inputFocus:"",
				ifChangeStatus:false,//是否改变订单状态
				SortCondition1:false,
				SortCondition2:false,
				SortCondition3:false,
				SortCondition4:false,
				SortCondition5:false,
				SortCondition6:false,
			}
		},
		mounted(){
			$("thead").find("td").append('<i class="iconfont"></i>')
		},
		created(){

			var that = this;

			if(this.$store.state.Replenish_TaskData.length){

				var Replenish_TaskData = this.$store.state.Replenish_TaskData;

				this.Replenish_TaskData = Replenish_TaskData;

				this.tasksNum = Replenish_TaskData.length;

				// 判断上一个页面，是否要改变列表订单状态
				if(this.$route.query.ifChangeStatus){

					this.ifChangeStatus = true;

				}else{
					this.ifChangeStatus = false;
				}

				console.log(this.ifChangeStatus)
				console.log(that.$store.state.Replenish_StatusArr)

				var RecordNum = 0;

				for(var i = 0 ; i < Replenish_TaskData.length ; i++){

					// 需要改变订单状态
					if(that.ifChangeStatus){

						Replenish_TaskData[i].Status = that.$store.state.Replenish_StatusArr[i];

					}else{

						that.$store.state.Replenish_StatusArr[i] = Replenish_TaskData[i].Status;
					}

					// 判断有多少重复库位
					if(i < Replenish_TaskData.length-2){

						if(Replenish_TaskData[i].ToLoc == Replenish_TaskData[i+1].ToLoc){

							RecordNum++;
						}
					}
				}

				// 库位数
				this.LibraryNum = Replenish_TaskData.length - RecordNum;
			}
		},
		methods:{
			//错误提示框消失
			tipsErrorHide:function (input,ifRouter){

				this.tipsHide = false ;

				if(ifRouter){
					// 补货完成  返回上一页
					this.$router.push({
						name:"ReplenishArea",
						query:{
						}
					})
				}

				if(input){

					this.$refs[input].select();
				}
			},
			// 返回上一页
			toArea:function(){

				//返回 区域/库位 页面
				this.$router.push({
					name:"ReplenishArea",
					query:{
					}
				})
			},
			// 开始补货
			Replenishment:function(){

				var that = this;

				var ReplenishIndex = this.detail_item;

				if(this.Replenish_TaskData[that.detail_item].Status==9){

					that.tipsShow();

					that.msg = "任务已完成";
					return;
				}
				//跳转到 补货页面
				this.$router.push({
					name:"Replenishment",
					query:{
						ReplenishIndex:ReplenishIndex
					}
				})
			},
			// 取消所有任务
			AbolishAllTasks:function(){

				var that = this;

				var AbolishTasksUrl = this.HOST + "RFRepDelTask.do";

				var AbolishTasksData = {
					TaskGroup:that.Replenish_TaskData[0].TaskGroup,
				}

				this.httpRequest(AbolishTasksUrl,AbolishTasksData,function(res){

					that.Replenish_TaskData = [];

					that.tipsShow();

					that.msg = "取消成功";

					that.ifRouter = true;//补货完成，返回上一页
				})
			},
			// 选择具体任务
			selectTask:function(index){

				this.detail_item = index;
			}
		}
	}
			// .tableHead{
			// 	position: fixed;
			// 	top:90/50rem;
			// 	left:0;
			// 	background-color: #fff;
			// 	z-index: 6;
			// }

			// margin-top: 50/50rem;
</script>

<style lang="less" scoped>
	.ReplenishTaskList{
		.container{
			margin-bottom: 150/50rem;
			.tableContainer{
				width: 100%;
				padding-bottom: 100/50rem;
			}
			tr{
				td{
					min-width: 80/50rem;
					padding:0 10/50rem;
					height: 65/50rem;
					margin:0;
					text-align: center;
	
				}
				td:nth-child(1){
					border-left: none;
				}
			}
			.selectedTask{
				background-color: #eee;
			}
			
		}
		.bottomNum{
		    font-size: 29/50rem;
			width: 100%;
			height:150/50rem;
			position: fixed;
			left: 0;
			bottom: 0;
			background-color: #fff;
			z-index: 5;
			&>div.taskNum{
				overflow: hidden;
				&>div{
					text-align: center;
					float: left;
					width: 375/50rem;
					height: 60/50rem;
					span{
						height: 60/50rem;
						margin-right:10/50rem;
					}
					input{
						width: 160/50rem;
						text-align: center;
						height: 45/50rem;
						font-size: 29/50rem;
						background-color:#f0f0f0;
						outline:none;
						border:none;
					}
				}
			}
			&>div.g_doubleBtn{
				margin-top: 0;
			}
			
		}
	}
	@media screen and (max-width: 330px){
		.ReplenishTaskList{
			.container{
				tr{
					td{
						min-width: 200/50rem;
					}
					td:nth-child(4),td:nth-child(5){
						min-width: 130/50rem;
					}
				}
			}
			.bottomNum{
				font-size: 38/50rem;
				&>div.taskNum{
					&>div{
						input{
							font-size: 38/50rem;
						}
					}
				}
			}
		}
	}
	@media screen and (max-width: 250px){
		.ReplenishTaskList{
			.container{
				tr{
					td{
						height: 105/50rem;
						min-width: 260/50rem;
					}
					td:nth-child(4),td:nth-child(5){
						min-width: 160/50rem;
					}
				}
			}
			.bottomNum{
				font-size: 42/50rem;
				&>div.taskNum{
					&>div{
						input{
							font-size: 42/50rem;
						}
					}
				}
			}
		}
	}
</style>


